html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-family: "PT Sans", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell",
    "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

.loader {
  color: rgb(0, 0, 255);
  text-align: center;
  font-size: 40px;
  padding-top: 30%;
}

.list {
  display: flex;
  font-weight: bold;
  justify-content: flex-start;
}

.navlist {
  list-style-type: none;
}

.navlink {
  color: rgb(0, 0, 255);
  padding-right: 30px;
  text-decoration: none;
  font-size: 20px;
}

.hrline {
  width: 100%;
  height: 2%;
  color: rgb(255, 235, 205);
}

.film_title {
  margin-left: 30px;
}

.film_description {
  display: flex;
  width: 60%;
}

.film_details {
  margin-left: 30px;
}

.movie_img {
  margin-left: 30px;
}

.back_button {
  margin-left: 30px;
  cursor: pointer;
  color: black;
  font-weight: bold;
  width: 100px;
  margin-bottom: 20px;
  text-transform: lowercase;
}

.details_additional {
  margin-left: 30px;
}

.input {
  width: 300px;
}
